.navigation {
  padding: 0 20px 20px 10px;

  @media (max-width: 1023px) {
    padding: 40px 28px;
  }

  @media (max-width: 727px) {
    align-items: center;
    display: grid;
    grid-template-columns: 1fr 52px;
    grid-template-rows: 72px 1fr;
    padding: 0 0 0 15px;
    border-bottom: 1px solid rgba(98, 105, 98, 0.2);
  }
}

.navigation__list {
  @include list-reset();
  column-width: 220px;
  column-gap: 4.5em;

  @media (max-width: 1023px) {
    column-width: auto;
  }

  @media (max-width: 727px) {
    grid-column: 1 / 3;
    grid-row: 2 / 3;
    padding: 0 0 15px 0;
  }
}

.navigation a {
  @include hover-focus-activity();
  font-family: "PT Mono", "Courier", monospace;
  font-size: 15px;
  line-height: 25px;
  opacity: 0.5;
}
