.modal-overlay {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 5;
  overflow: scroll;

  @media (max-width: 1023px) {
    display: none;
  }
}

.modal--hidden {
  display: none;
}

.modal {
  position: fixed;
  left: calc(50% - 245px);
  top: 135px;
  width: 490px;
  background-color: #00b2ff;
  padding: 30px 40px 15px 40px;
  z-index: 6;
}

.modal h2 {
  margin: 0;
  font-size: 28px;
  line-height: 40px;
  // letter-spacing: 0.02em;
  font-weight: 400;
  font-stretch: semi-condensed;
}

.modal p {
  font-family: "PT Mono", "Courier", monospace;
  margin: 12px 0;
  font-size: 16px;
  line-height: 22px;
  width: 95%;
}

.modal input {
  padding: 0 25px;
}

.modal textarea {
  min-height: 55px;
  padding: 15px 25px;
}

.modal__close-button {
  @include hover-focus-activity();
  position: absolute;
  top: 5px;
  right: -8px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
